* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

body {
  background-color: #00000008;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 20px;
}

h3 {
  margin: 0 0 10px;
  font-size: 18px;
  font-weight: 500;
}

h3 a {
  color: #000;
  text-decoration: none;
}

h3 a:hover {
  text-decoration: underline;
}

p {
  margin: 0 0 10px;
  font-size: 16px;
}

input {
  border: 1px solid #d3d3d3;
  border-radius: 50px;
  outline: none;
  width: 100%;
  padding: 7px 20px;
  font-size: 18px;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  text-align: center;
  padding: 20px 0;
}

header svg {
  max-width: 250px;
}

.input-group {
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

.input-group button {
  cursor: pointer;
  background: #9fba29;
  border: 1px solid #d3d3d3;
  border-radius: 0 50px 50px 0;
  height: 38px;
  padding: 5px 10px;
  position: absolute;
  right: 0;
}

.input-group button img {
  filter: brightness() invert();
  max-width: 100%;
  max-height: 100%;
}

.input-group #errorMessage {
  color: red;
}

#resultsContainer {
  flex-wrap: wrap;
  column-gap: 20px;
  margin: 30px 0;
  display: flex;
}

#resultsContainer .result-column {
  word-break: break-word;
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  margin: 20px 0;
  padding: 25px 20px;
  box-shadow: 0 0 3px #0003;
}

@media (width >= 992px) {
  #resultsContainer .result-column {
    width: calc(50% - 10px);
    max-height: 400px;
    margin-bottom: 30px;
    overflow-y: scroll;
  }
}

#resultsContainer .result-column .card-header {
  border-bottom: 2px solid #d3d3d3;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  padding-bottom: 20px;
  display: flex;
}

#resultsContainer .result-column .card-header img {
  max-width: 40px;
}

#resultsContainer .result-column .card-header h2 {
  margin: 0;
  padding-left: 10px;
  font-weight: 500;
}

#resultsContainer .result-column .result-card {
  border-bottom: 1px solid #d3d3d3;
  align-items: flex-start;
  margin-bottom: 30px;
  padding-bottom: 10px;
  display: flex;
}

#resultsContainer .result-column .result-card:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

#resultsContainer .result-column.has-error p {
  font-size: 18px;
}

.youtube-column .result-card > a {
  padding-right: 15px;
}

.stackoverflow-column .info {
  text-align: right;
  min-width: 90px;
  padding-right: 20px;
}

.stackoverflow-column .info .ans-count {
  color: #fff;
  white-space: nowrap;
  background-color: #2f6f44;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 12px;
  display: inline-block;
}

